<template>
    <div style="padding: 10px">
        <!--    -->
        <div style="margin: 10px 0">
            <el-button type="primary" @click="add">
                <el-icon>
                    <CirclePlus/>
                </el-icon>
                &nbsp
            </el-button>

        </div>
        <!--    -->
        <div style="margin: 10px 0;display: block;clear: both">
            <el-input v-model="title" placeholder="" style="width: 20%" class="mr-10" :prefix-icon="Search"
                      clearable/>
            <el-input v-model="author" placeholder="" style="width: 20%" :prefix-icon="Search" class="mr-10"
                      clearable/>
            <el-button type="primary" class="mb-10" @click="load"></el-button>
            <el-button type="primary" class="mb-10" @click="reset"></el-button>
            <el-popconfirm title="" @confirm="deleteBatch">
                <template #reference>
                    <el-button class="mb-10" type="danger" style="float: right;margin-right: 10px">
                        <el-icon>
                            <Delete/>
                        </el-icon>
                        &nbsp
                    </el-button>
                </template>
            </el-popconfirm>


        </div>
        <!--    -->
        <!--        stripe:-->
        <el-table :data="tableData" border stripe style="width: 100%" @selection-change="handleSelectionChange"
                  highlight-current-row>
            <el-table-column type="selection" width="40px"/>
            <!--            sortable:-->
            <el-table-column prop="id" label="ID" sortable=""/>
            <el-table-column prop="title" label=""/>
            <el-table-column prop="author" label=""/>
            <el-table-column prop="createTime" label=""/>
            <el-table-column fixed="right" label="" width="300px">
                <template #default="scope">
                    <el-button plain type="success" @click="details(scope.row)"></el-button>
                    <el-button plain type="primary" @click="handleEdit(scope.row)"></el-button>
                    <el-popconfirm title="?" @confirm="handleDelete(scope.row)">
                        <template #reference>
                            <el-button type="danger">
                                <el-icon>
                                    <Delete/>
                                </el-icon>
                            </el-button>
                        </template>
                    </el-popconfirm>
                </template>
            </el-table-column>
            <el-pagination small layout="prev, pager, next" :total="50"/>
        </el-table>
        <div style="margin: 10px 0">
            <el-pagination
                    v-model:currentPage="currentPage"
                    v-model:page-size="pageSize"
                    :page-sizes="[10, 20, 50]"
                    :small="small"
                    :disabled="disabled"
                    :background="background"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total"
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
            />
            <el-dialog v-model="dialogVisible" title="" width="50%">
                <el-form model="form" label-width="120px">
                    <el-form-item label="">
                        <el-input v-model="form.title" style="width: 50%"/>
                    </el-form-item>
                    <div id="div1">

                    </div>
                </el-form>
                <template #footer>
                    <span class="dialog-footer">
                        <el-button @click="dialogVisible = false"></el-button>
                        <el-button type="primary" @click="save"></el-button>
                    </span>
                </template>
            </el-dialog>
            <el-dialog v-model="vis" title="" width="50%">
                <el-card>
                    <div v-html="detail.content" style="min-height: 100px"></div>
                </el-card>
            </el-dialog>
        </div>

    </div>
</template>

<script>

    import request from "../utils/request";
    import {Search, Delete} from "@element-plus/icons-vue";
    import '@wangeditor/editor/dist/css/style.css' //  css

    import E from 'wangeditor'
    import {serverIp} from "../../public/config";
    //
    let editor;
    let userStr = sessionStorage.getItem("user") || "{}"
    let user = JSON.parse(userStr)

    export default {
        name: 'Events',
        components: {},
        data() {
            return {
                form: {},
                dialogVisible: false,
                title: '',
                author: '',
                currentPage: 1,
                pageSize: 10,
                total: 10,
                tableData: [],
                detail: {},
                vis: false,
                editor: null,
                ids: []
            }
        },
        created() {
            this.load();
        },
        setup() {
            return {
                Search,
                Delete
            }
        },
        methods: {
            details(row) {
                this.detail = row
                this.vis = true
            },
            reset() {
                this.title = '';
                this.author = '';
                this.load();
            },
            load() {
                request.get("/events", {
                    params: {
                        pageNum: this.currentPage,
                        pageSize: this.pageSize,
                        title: this.title,
                        author: this.author,
                        // createTime: this.createTime
                    }
                }).then(res => {
                    // console.log(res);
                    this.tableData = res.data.records;
                    this.total = res.data.total;
                })
            },
            add() {
                this.dialogVisible = true;
                // 
                this.form = {};
                //
                this.$nextTick(() => {
                    if (!editor) {
                        //adddivneweditor
                        editor = new E('#div1');
                        //json
                        editor.config.uploadImgServer = 'http://'+serverIp+':9090/files/editor/upload';
                        editor.config.uploadFileName = "file";
                        editor.create()
                    }
                    editor.txt.html("")
                })

            },
            save() {
                this.form.content = editor.txt.html(); //form

                if (this.form.id) {//id
                    let userStr = sessionStorage.getItem("user") || "{}";
                    let user = JSON.parse(userStr)
                    // console.log(""+this.form.author+","+user.name)
                    if (this.form.author === user.name) {
                        request.put("/events", this.form).then(res => {
                            // console.log(res);
                            if (res.code === '0') {
                                this.$message.success("")
                            } else {
                                this.$message.error(res.msg)
                            }
                            this.load();//
                            this.dialogVisible = false
                        });
                    } else {
                        this.$message.error("")
                    }

                } else {//id

                    this.form.author = user.name
                    request.post("/events", this.form).then(res => {
                        // console.log(res);
                        if (res.code === '0') {
                            this.$message.success("")
                        } else {
                            this.$message.error(res.msg)
                        }
                        this.load();//
                        this.dialogVisible = false
                    });
                }
            },
            handleEdit(row) {
                this.form = JSON.parse(JSON.stringify(row));
                this.dialogVisible = true

                this.$nextTick(() => {
                    if (!editor) {
                        editor = new E('#div1');
                        editor.config.uploadImgServer = 'http://'+serverIp+':9090/files/editor/upload';
                        editor.config.uploadFileName = "file";
                        editor.create()
                    }
                    editor.txt.html(row.content)
                })
            },
            handleDelete(row) {

                this.id = row.id;
                // console.log(this.id);
                request.delete("/events/" + this.id).then(res => {
                    if (res.code === '0') {
                        this.$message.success("")
                    } else {
                        this.$message.error(res.msg)
                    }
                    this.load();//
                })
            },
            handleSizeChange() {
                //
                this.load()
            },
            handleCurrentChange() {
                //
                this.load()
            },
            deleteBatch() {
                if (!this.ids.length) {
                    this.$message.warning("");
                    return
                }
                request.post("/events/deleteBatch", this.ids).then(res => {
                    if (res.code === '0') {
                        this.$message.success("")
                        this.load()
                    } else {
                        this.$message.error(res.msg)
                    }
                })
            },
          handleSelectionChange(val) {
                this.ids = val.map(v => v.id)
            },
        }
    }
</script>
